video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}


.hero-container {
     background: url('C:\Users\Samalouty\Desktop\maze project\samalouty.github.io\public\images\img-10.jpg') center center/cover
    no-repeat; 
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode',
  'Lucida Sans', Arial, Helvetica, sans-serif;
  font-size: 20px;
  line-height: 1.5;
  text-align: center;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.hero-container > p::before {
  content: "";
  display: block;
  width: 60px;
  height: 2px;
  background-color: #e2a100; /* Highlight color */
  margin: 0 auto 15px;
}

.hero-container > p::after {
  object-fit: cover;
  content: "";
  display: block;
  width: 30px;
  height: 2px;
  background-color: #e2a100; /* Highlight color */
  margin: 15px auto 0;
}


.hero-btns{
    margin-top: 32px;
}


.hero-btns .btn {
    margin: 6px;
}

.fa-play-circle{
    margin-left: 4px;
}

@media screen and (max-width: 960px) {
    .hero-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .hero-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .hero-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }
